.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


.curentData {
  /* padding-left: 5%; */
  padding-top: 14px
}

.forcast{
  display: flex;
  flex-direction: row;
}

.forcast_element{
  padding: 12px 30px;
}

.date_time{
  white-space:nowrap
}
.current_temp {
  font-size: 4rem;
}

.location{
  font-size: 2rem;
}

.heading{
  border-bottom: 3px solid white;
    width: 100%;
    margin: auto;
}

.wether_icon{
  height: 15vh;
}